import {
  Button,
  FormControl,
  InputLabel,
  MenuItem,
  Select,
} from '@mui/material';
import Papa from 'papaparse';
import React, { useContext, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { DataContext } from '..';
import DomainList from '../components/DomainList';

const Home = () => {
  const dataArray = useContext(DataContext);
  const [domains, setDomains] = useState([]);
  const [timeFrame, setTimeFrame] = useState('');
  const [uploading, setUploading] = useState(false);
  const [show, setShow] = useState(false);
  const navigate = useNavigate();

  const handleFileUpload = (e) => {
    setShow(false);
    setUploading(true);
    const file = e.target.files[0];
    Papa.parse(file, {
      complete: (results) => {
        const newArray = results.data.slice(1, results.data.length - 1);
        setDomains(newArray);
        dataArray[0] = newArray;
        if (dataArray[1]) {
          dataArray.splice(1, 1);
        }
      },
    });
  };

  const handleChange = (event) => {
    setTimeFrame(event.target.value);
    dataArray[2] = {
      timeFrame: event.target.value,
    };
  };

  // useEffect(() => {}, []);

  return (
    <div
      style={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        height: '100vh',
      }}>
      {!uploading && (
        <Button
          variant='contained'
          component='label'
          sx={{ margin: '20px' }}
          onClick={() => {
            navigate('/result');
            setUploading(false);
          }}>
          Go to results
        </Button>
      )}
      <div
        style={{
          maxWidth: '500px',
          display: 'flex',
          flexDirection: 'row',
          justifyContent: 'space-between',
          alignItems: 'center',
        }}>
        <Button
          variant='contained'
          component='label'
          sx={{ margin: '20px', height: '56px' }}>
          Upload Domain List
          <input onChange={handleFileUpload} hidden accept='.csv' type='file' />
        </Button>
        <FormControl sx={{ width: '160px' }}>
          <InputLabel id='demo-simple-select-label'>Time Frame</InputLabel>
          <Select
            labelId='demo-simple-select-label'
            id='demo-simple-select'
            value={timeFrame}
            label='Time Frame'
            onChange={handleChange}>
            <MenuItem value={'all'}>All Data</MenuItem>
            <MenuItem value={'old'}>One year back data</MenuItem>
          </Select>
        </FormControl>
      </div>
      <br />
      <DomainList domains={dataArray[0]} />
      {uploading && timeFrame && (
        <Button
          variant='contained'
          component='label'
          sx={{ margin: '20px', height: '56px' }}>
          Get URL Structure
          <input
            onClick={() => {
              if (domains.length > 0) {
                // set uploaded domains to context
                navigate('/result');
                setShow(true);
              } else {
                alert('Please upload a domain list');
              }
            }}
            type='button'
            hidden
          />
        </Button>
      )}
    </div>
  );
};

export default Home;
